import Vue from 'vue'
import Vuex from 'vuex'
// import home from './modules/home'

Vue.use(Vuex)

const store = new Vuex.Store({
    state: {
      isCollapse: false
    },
    mutations: {
      increment (state) {
        // 变更状态
        state.isCollapse = !state.isCollapse;
      }
    }
})

export default store


