import Vue from 'vue'
import Router from 'vue-router'
import Layout from '../views/Layout.vue'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Layout',
      component: Layout,
      redirect: '/InformationManagement',
      children: [
        {
          path: '/InformationManagement',
          name: 'InformationManagement',
          component: () => import('../views/InformationManagement.vue'),
          meta: {
            requireLogin: true //进入该路由需要登录
          }
        },
        {
          path: '/BrandManagement',
          name: 'BrandManagement',
          component: () => import('../views/BrandManagement.vue'),
          meta: {
            requireLogin: true //进入该路由需要登录
          }
        },
        {
          path: '/StockManagement',
          name: 'StockManagement',
          component: () => import('../views/StockManagement.vue'),
          meta: {
            requireLogin: true //进入该路由需要登录
          }
        },
      ],
      meta: {
        requireLogin: true //进入该路由需要登录
      }
    },
    {
      path: '/Login',
      name: 'Login',
      component: () => import('../views/Login.vue')
    },
  ]
})

//利用vue-router提供的钩子函数beforeEach()对路由进行判断
router.beforeEach((to, from, next) => {
  if (to.meta.requireLogin){
    if (window.sessionStorage.getItem("username")){
      next()
    }
    else {
      //未登录时跳转到登录页面
      next({
        path: "login"
      })
    }
  }
  else {
    next()
  }
})

export default router



