<template>
  <div class="layout">   
    <el-row class="tac">
      <el-col :span="isCollapse?1:4" style="height: 100vh">
        <el-menu
          :collapse-transition=false
          :collapse="isCollapse"
          default-active="1-1"
          background-color="rgb(238, 241, 246)"
          class="el-menu-vertical-demo">
          <h4 v-if="this.isCollapse" class="layout-title"><img src="@/assets/images/car.png" alt=""></h4>
          <h4 v-else class="layout-title">汽车管理系统</h4>
          <el-submenu index="1">
            <template slot="title">
              <i class="el-icon-truck"></i>
              <span>汽车管理</span>
            </template>
            <el-menu-item-group>
              <el-menu-item index="1-1" @click="toInfoManage">汽车信息管理</el-menu-item>
              <el-menu-item index="1-2" @click="toBrandManage">汽车品牌管理</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-menu-item index="2" @click="toStockManage">
            <i class="el-icon-house"></i>
            <span slot="title">库存管理</span>
          </el-menu-item>
        </el-menu>
      </el-col>
      <el-col :span="isCollapse?23:20" :class="isCollapse?'openMeau':'closeMeau'">
        <div class="layout-icon row" @click="expandMenu">
          <i :class="isCollapse?'el-icon-arrow-right':'el-icon-arrow-left'"></i>
          <span class="logOut" @click="logOut">退出登录</span>
        </div>
      </el-col>
      <el-col :span="isCollapse?23:20" :class="isCollapse?'openMeau':'closeMeau'">
        <router-view></router-view>
      </el-col>
    </el-row>
    

  </div>
</template>
<script>

  export default {
    name: "Layout",
    components: {
     
    },
    data () {
      return {
        // isCollapse: false
      }
    },
    computed: {
      isCollapse () {
        return this.$store.state.isCollapse
      }
    },
    methods: {
      // 展开关闭菜单
      expandMenu () {
        this.$store.commit('increment')
      },
      //路由到信息管理
      toInfoManage () {
        this.$router.push("/InformationManagement")
      },
      //路由到品牌管理
      toBrandManage () {
        this.$router.push("/BrandManagement")
      },
      //路由到库存管理
      toStockManage () {
        this.$router.push("/StockManagement")
      },
      logOut () {
        this.$router.push("/LogIn")
        window.sessionStorage.removeItem("username")
      },
    }
  }
</script>

<style>
  .layout{
    width: 100%;
    height: 100%;
    position: relative;
  }
  .tac{
    height: 100%;
  }
  .layout-title{
    margin: 0;
    padding: 20px 0;
    height: 21px;
    text-align: center;
    background-color: #f6db63;
  }
  .layout-title img{
    width: 24px;
    height: 21px;
  }
  .el-menu-vertical-demo{
    height: 100%;
  }
  .el-menu--collapse{
    width: 99%;
  }
  .layout-icon{
    padding-left: 20px;
    position: relative;
    top: 0;
    height: 61px;
    justify-content: left;
    background-color: #f6db63;
  }
  .layout-icon i{
    cursor: pointer;
  }
  .logOut{
    position: absolute;
    right: 50px;
    cursor: pointer;
  }
</style>


